<template>
  <div class="home">
    <div class="container">
      <div class="card">
        <ul class="stepper">
          <li><i class="active">1</i> <span>Delivery</span></li>
          <li><i class="active">2</i> <span>Payment</span></li>
          <li><i>3</i> <span>Finish</span></li>
        </ul>
        <div class="card-header">
          <router-link to="/">Back to delivery</router-link>
        </div>
        <div class="card-body">
            <div class="card-left">

<div class="form-title">
                <h2>Shipment</h2>
              </div>
              <div class="form-body">
                <div class="input-radio-group">
                  <label v-for="(courier, index) in couriers" :key="index">
                    <input type="radio" class="radio-input" v-model="shipment" :value="courier.id">
                    <div class="radio-element">
                      <div class="">
                      <p>{{ courier.name }}</p>
                      <small>{{ courier.price }}</small>
                      </div>
                    </div>
                  </label>
                </div>
                
              </div>

              <div class="form-title">
                <h2>Payment</h2>
              </div>
              <div class="form-body">
                <div class="input-radio-group">
                  <label>
                      <input type="radio" class="radio-input" v-model="paymentMethod" value="e-Wallet">
                      <div class="radio-element">
                        <div class="">
                          <p>e-Wallet</p>
                          <small>1.500.000 left</small>
                        </div>
                      </div>
                  </label>

                  <label>
                      <input type="radio" class="radio-input" v-model="paymentMethod" value="Bank Transfer">
                      <div class="radio-element">
                        <p>Bank Transfer</p>
                      </div>
                  </label>

                  <label>
                      <input type="radio" class="radio-input" v-model="paymentMethod" value="Virtual Account">
                      <div class="radio-element">
                        <p>Virtual Account</p>
                      </div>
                  </label>
                </div>
                
              </div>
            </div>
            <div class="card-right">

              <h4>Summary</h4>
                <p class="m-0"><small>10 items purchased</small></p>

                <h5>Delivery Estimation</h5>
                <p class="m-0"><small>{{ shipmentEstimate }} by {{ shipmentName }}</small></p>

                <table class="table-summary">
                  <tr>
                    <td>Cost of goods</td>
                    <td align="right">{{ costGoods }}</td>
                  </tr>
                  <tr>
                    <td>Dropshipping fee</td>
                    <td align="right">{{ feeDropshipping }}</td>
                  </tr>
                  <tr>
                    <td>{{ shipmentName }} shipment</td>
                    <td align="right">{{ shipmentPrice }}</td>
                  </tr>
                  <tr>
                    <td><h3>Total</h3></td>
                    <td align="right"><h3>{{ costGoods + feeDropshipping + shipmentPrice }}</h3></td>
                  </tr>
                </table>
                <!-- <router-link to="/finish" class="btn">Pay with {{ payment }}</router-link> -->
                <button type="button" class="btn" @click="submit">Pay with {{ paymentMethod }}</button>

            </div>


        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PaymentView",
  data(){
    return {
      dataOrder: '',
      shipment: '',
      shipmentName: '',
      shipmentPrice: 0,
      shipmentEstimate: '',
      payment: '',
      costGoods: 0,
      feeDropshipping: 0,
      total: 0,
      couriers: [
        {
          id: 1,
          name: 'GO-SEND',
          price: 15000,
          estimate: 'today'
        },
        {
          id: 2,
          name: 'JNE',
          price: 9000,
          estimate: '2 day'
        },
        {
          id: 3,
          name: 'Personal Courier',
          price: 29000,
          estimate: '1 day'
        },
      ],
      paymentMethod: ''
    }
  },
  watch: {
    txtDropshipper(){
      this.chooseDropshipper()
    },
    shipment(val){
  
      this.couriers.map((x) => {
        if(x.id == val){
          this.shipmentName = x.name
          this.shipmentPrice = x.price
        }
      })
    }
  },
  mounted(){
    this.getDataOrder()
  },
  methods: {
    getDataOrder(){
      this.dataOrder = JSON.parse(localStorage.getItem('dataOrder'))
      if(this.dataOrder ){
        console.log('ada ' + this.dataOrder .costGoods)
        this.costGoods = this.dataOrder .costGoods
        this.feeDropshipping = this.dataOrder .dropshippingFee
      }else{
        console.log('tidak')
      }
    },
    chooseDropshipper(){
      if(this.txtDropshipper != false){
        this.feeDropshipping = 5900
        this.isDisabled = false
      }else{
        this.txtDropshipperName = ''
        this.txtDropshipperPhone = ''
        this.feeDropshipping = 0
        this.isDisabled = true
      }
    },
    phoneValidator(e){
      const str = e.target.value
      console.log(str)
      let reg = /^[0-9()+-]+$/
      let result = reg.test(str)
      if(result){
        if(str.length < 6){
           e.target.classList.remove('valid')
          e.target.classList.add('invalid')
          
        }else if(str.length > 20){
          e.target.classList.remove('valid')
          e.target.classList.add('invalid')
        }else{
         e.target.classList.remove('invalid')
          e.target.classList.add('valid')
        }
      }else{
        e.target.classList.remove('valid')
        e.target.classList.add('invalid')
      }
    },
    emailValidator(e){
      const str = e.target.value
      //eslint-disable-next-line
      let reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
      let result = reg.test(str)
      if(result){
         e.target.classList.remove('invalid')
          e.target.classList.add('valid')
      }else{
        e.target.classList.remove('valid')
        e.target.classList.add('invalid')
      }
    },
    generateId(digit){
      var id = ''
      var string = 'ABCDEFGHIJKLMNPQRSTUVWXYZabcdefghijkmnpqrstuvwxyz23456789'
      var stringLength = string.length
      for(var i = 0; i < digit; i++){
        id += string.charAt(Math.floor(Math.random() * stringLength))
      }
      return id
    },
    submit(){
      this.dataOrder.orderId = this.generateId(5)
      this.dataOrder.total = this.shipmentPrice + this.costGoods + this.feeDropshipping
      this.dataOrder.courierName = this.shipmentName
      this.dataOrder.courierPrice = this.shipmentPrice
      this.dataOrder.courierEstimate = this.shipmentEstimate
      this.dataOrder.paymentMethod = this.paymentMethod
      localStorage.setItem('dataOrder', JSON.stringify(this.dataOrder))
      this.$router.push('/finish')
    }
  }
};
</script>
