<template>
  <div class="home">
    <div class="container">
      <div class="card">
        <ul class="stepper">
          <li><i class="active">1</i> <span>Delivery</span></li>
          <li><i>2</i> <span>Payment</span></li>
          <li><i>3</i> <span>Finish</span></li>
        </ul>
        <div class="card-header">
          <router-link to="/">Back to cart</router-link>
        </div>
        <div class="card-body">
          <div class="card-left">
            <div class="form-title">
                <h2>Delivery details</h2>
                <div>
                  <input type="checkbox" id="dropshipper"  v-model="txtDropshipper"> <label for="dropshipper">Send as dropshipper</label>
                </div>
              </div>
            <div class="form-body">
                <div class="form-left">
                  <div class="form-group">
                    <input type="text" class="" v-model="txtEmail" @keyup="emailValidator" placeholder="Email" required="">
                    <!-- <span>Email</span> -->
                  </div>
                  <div class="form-group">
                    <input type="text" class="" v-model="txtPhone" @keyup="phoneValidator"  placeholder="Phone Number" required="">
                    <!-- <span>Phone Number</span> -->
                  </div>
                  <div class="form-group">
                    <textarea rows="3"  v-model="txtAddress" :maxlength="maxAddress"  placeholder="Address"></textarea>
                    <small>Sisa karakter {{ maxAddress - txtAddress.length }}</small>
                    <!-- <span>Delivery Address</span> -->
                  </div>
                </div>
                <div class="form-right">
                  <div class="form-group">
                    <input type="text" v-model="txtDropshipperName" :disabled="isDisabled"  placeholder="Dropshipper Name">
                  </div>
                  <div class="form-group">
                    <input type="text" v-model="txtDropshipperPhone" :disabled="isDisabled"  placeholder="Dropshipper Phone">
                  </div>
                </div>
                
              </div>
          </div>
          <div class="card-right">
            <h4>Summary</h4>
                <p class="m-0"><small>10 items purchased</small></p>

                <table class="table-summary">
                  <tr>
                    <td>Cost of goods</td>
                    <td align="right">{{ costGoods }}</td>
                  </tr>
                  <tr>
                    <td>Dropshipping fee</td>
                    <td align="right">{{ feeDropshipping }}</td>
                  </tr>
                  <tr>
                    <td><h3>Total</h3></td>
                    <td align="right"><h3>{{ costGoods + feeDropshipping }}</h3></td>
                  </tr>
                </table>
                <button type="button" class="btn" @click="submit">Continue to Payment</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeView",
  data(){
    return {
      txtEmail: '',
      txtPhone: '',
      txtAddress: '',
      txtDropshipper: '',
      txtDropshipperName: '',
      txtDropshipperPhone: '',
      costGoods: 500000,
      feeDropshipping: 0,
      total: 0,
      isDisabled: true,
      maxAddress: 120,
    }
  },
  watch: {
    txtDropshipper(){
      this.chooseDropshipper()
    }
  },
  methods: {
    chooseDropshipper(){
      if(this.txtDropshipper != false){
        this.feeDropshipping = 5900
        this.isDisabled = false
      }else{
        this.txtDropshipperName = ''
        this.txtDropshipperPhone = ''
        this.feeDropshipping = 0
        this.isDisabled = true
      }
    },
    phoneValidator(e){
      const str = e.target.value
      console.log(str)
      let reg = /^[0-9()+-]+$/
      let result = reg.test(str)
      if(result){
        if(str.length < 6){
           e.target.classList.remove('valid')
          e.target.classList.add('invalid')
          
        }else if(str.length > 20){
          e.target.classList.remove('valid')
          e.target.classList.add('invalid')
        }else{
         e.target.classList.remove('invalid')
          e.target.classList.add('valid')
        }
      }else{
        e.target.classList.remove('valid')
        e.target.classList.add('invalid')
      }
    },
    emailValidator(e){
      const str = e.target.value
      //eslint-disable-next-line
      let reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
      let result = reg.test(str)
      if(result){
         e.target.classList.remove('invalid')
          e.target.classList.add('valid')
      }else{
        e.target.classList.remove('valid')
        e.target.classList.add('invalid')
      }
    },
    submit(){
      var dataOrder = {
        orderId: '',
        email: this.txtEmail,
        phone: this.txtPhone,
        address: this.address,
        dropshipperName: this.txtDropshipperName,
        dropshipperPhone: this.txtDropshipperPhone,
        isDropshipper: this.isDropshipper,
        costGoods: this.costGoods,
        dropshippingFee: this.feeDropshipping,
        total: this.total,
        courierName: '',
        courierPrice: 0,
        courierEstimate: '',
        paymentMethod: '',
        paymentFee: 0
      }
      localStorage.setItem('dataOrder', JSON.stringify(dataOrder))

      this.$router.push('/payment')
    }
  }
};
</script>
