<template>
  <div class="home">
    <div class="container">
      <div class="card">
        <ul class="stepper">
          <li><i class="active">1</i> <span>Delivery</span></li>
          <li><i class="active">2</i> <span>Payment</span></li>
          <li><i class="active">3</i> <span>Finish</span></li>
        </ul>
        <div class="card-header">
          <!-- <router-link to="/">Back to delivery</router-link> -->
        </div>
        <div class="card-body">
          <div class="card-left">

              <h3>Thank you</h3>
              <p><strong> Order ID : {{ dataOrder.orderId }}</strong><br>
              <small>Your order will be delivered today with gosend</small></p>
              <router-link to="/">Go to homepage</router-link>

          </div>
          <div class="card-right">

            <h4>Summary</h4>
                <p class="m-0"><small>10 items purchased</small></p>

                <h5>Delivery Estimation</h5>
                <p class="m-0"><small>{{ dataOrder.courierEstimate }} by {{ dataOrder.courierName }}</small></p>

                <h5>Payment Method</h5>
                <p class="m-0"><small>{{ dataOrder.paymentMethod }}</small></p>

                <table class="table-summary">
                  <tr>
                    <td>Cost of goods</td>
                    <td align="right">{{ dataOrder.costGoods }}</td>
                  </tr>
                  <tr>
                    <td>Dropshipping fee</td>
                    <td align="right">{{ dataOrder.dropshippingFee }}</td>
                  </tr>
                  <tr>
                    <td>{{ dataOrder.courierName }} shipment</td>
                    <td align="right">{{ dataOrder.courierPrice }}</td>
                  </tr>
                  <tr>
                    <td><h3>Total</h3></td>
                    <td align="right"><h3>{{ dataOrder.total }}</h3></td>
                  </tr>
                </table>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PaymentView",
  data(){
    return {
        dataOrder: '',
    }
  },
  watch: {
    
  },
   mounted(){
    this.getDataOrder()
  },
  methods: {
    getDataOrder(){
      var dataOrder = JSON.parse(localStorage.getItem('dataOrder'))
      if(dataOrder){
        this.dataOrder = dataOrder
      }else{
        this.dataOrder = ''
      }
    },
  }
};
</script>
